.cabecalho {
  border-radius: 32px;
  height: 8vh;
  width: 90vw;
  margin: 0 auto;
  background: linear-gradient(
    to right bottom,
    rgb(0, 127, 255),
    rgb(0, 89, 178) 120%
  );
  box-shadow: 1px 1px 5px rgb(2, 22, 27);
}

.cabecalho__titulo {
  font-size: 1.8rem;
  line-height: 7.5vh;
  font-family: var(--roboto_mono);
  font-style: italic;
  text-align: center;
  color: #fefefe;
}
